
.owl-theme {
	// Styling Next and Prev buttons
	.owl-nav {
		margin-top: 10px;
		text-align: center;
		-webkit-tap-highlight-color: transparent;

		[class*='owl-'] {
			color: $owl-nav-color;
			font-size: $owl-nav-font-size;
			margin: $owl-nav-margin;
			padding: $owl-nav-padding;
			background: $owl-nav-background;
			display: inline-block;
			cursor: pointer;
			border-radius: 3px;

			&:hover {
				background: $owl-nav-background-hover;
				color:$owl-nav-color-hover;
				text-decoration: none;
			}
		}
		.disabled {
			opacity: $owl-nav-disabled-opacity;
			cursor: default;
		}
	}

	// Styling dots
	.owl-nav.disabled + .owl-dots {
		margin-top: 10px;
	}

	.owl-dots {
		text-align: center;
		-webkit-tap-highlight-color: transparent;

		.owl-dot {
			display: inline-block;
			zoom: 1;
			*display: inline;
			outline: none;

			span {
				position: relative;
				width: 16px;
				height: 16px;
				margin-right: 15px;
				display: block;
				border: $blue 1px solid;
				border-radius: 50%;	
				outline: none;	
				transition: border 0.3s ease-in-out;
			}

			&.active {
				span {
					
					&::before {
						content: '';
						position: absolute;
						left: 3px;
						top: 3px;
						z-index: 2;
						background-color: $blue;
						width: 8px;
						height: 8px;
						border-radius: 50%;
					}
				}
			}

			&:hover {
				span {
					border: $darkblue 1px solid;
				}
			}
		}
	}
}
